      'Treiber vom Typ 1: \n\nJDBC-ODBC-Bridge Bei diesem Treibertyp greift der JDBC-Treiber ber die ODBC-Schnittstelle auf die Datenbank zu. Natrlich muss dazu auf jedem Client ein ODBC-Treiber installiert sein. Dieser Treibertyp bietet sich daher hauptschlich in lokalen Netzwerken an, bei denen auf dem einzelnen Client leicht ODBC installiert werden kann. Auerdem ist durch den Zwischenschritt ODBC der Zugriff verhltnismig langsam.\n\n\nTreiber vom Typ 2: \nNative-API partly-Java-Treiber Bei diesem Ansatz werden die JDBC-Aufrufe in ein datenbankspezifisches API auf dem Client weitergeben. Daher sind auch bei diesem Typ zustzliche Installationen auf dem Client notwendig.\n\nTreiber vom Typ 3: \nJDBC-Net pure Java-Treiber Treiber vom Typ 3 bersetzen die JDBC-Aufrufe in ein vom DBMS unabhngiges Netzprotokoll und benutzen einen Anwendungsserver. Der Server bersetzt die Aufrufe in das jeweilige DBMS-Protokoll und ber die CLI-Schnittstelle wird auf die Datenbank zugegriffen. Bei Treibern vom Typ 3 ist keine zustzliche Client-Installation notwendig, aber ein Anwendungsserver.\n\n\nTreiber vom Typ 4:\nNative-Protocol pure Java-Treiber Bei diesem Treibertyp werden die JDBC-Aufrufe in ein datenbankeigenes Protokoll bersetzt, welches ber das Netzwerk direkt auf den Datenbankserver zugreift. Es ist auch keine zustzliche Client- Installation notwendig.  \n\n\nTreiber von Typ 3 und 4 sind appletfhig, die anderen beiden Typen nicht, da sie nicht komplett in Java geschrieben sind.',
