    category: 4,
    difficulty: 3,
    text: 'Welche Regeln gehren zu den zwlf Basisregeln, die E.F. Codd fr relationale Datenbanksysteme aufstellte?',
    explanation:
      'E. F. Codd, einer der Begrnder der relationalen Datenbanken, beschreibt\nzwlf Basisregeln, die eine relationale Datenbank auszeichnen,\nsich aber auch auf andere Datenbanktypen verallgemeinern lassen\n\nRegel 1: Informationsregel\nJede Information einer relationalen Datenbank wird in genau einer Weise durch Werte in\nRelationen dargestellt und damit redundanzfrei und einheitlich verwaltet. \n\nRegel 2: Garantierter Zugriff\nJedes einzelne Feld der Datenbank ist durch eine Kombination von Relationsname,\nPrimrschlssel und Spaltenname erreichbar.\n\nRegel 3: Systematische Behandlung fehlender Information\nIn einer relationalen Datenbank mssen Spalten mit fehlender Information (Nullwerte)\neinheitlich darstellbar sein. Diese Nullwerte werden systematisch als fehlende\nInformation von den Standardwerten (z.B. Strings mit Leerzeichen) unterschieden.\nSpalten knnen auch so eingerichtet werden, dass Nullwerte nicht erlaubt sind.\n\nRegel 4: Dynamischer Online-Katalog (Data Dictionary)\nEin Datenbankschema wird in derselben Weise wie die gespeicherten Daten selbst\nbeschrieben  nmlich in Tabellen, dem sogenannten Data Dictionary. Autorisierte\nBenutzer knnen das Data Dictionary in gleicher Weise abfragen wie die eigentliche\nDatenbasis.\n\nRegel 5: Allumfassende Sprache\nEin Datenbanksystem muss eine Sprache untersttzen, die allumfassend sein, d.h. sie \nerfllt die folgenden Aufgaben:\nDefinition der Benutzerdaten,\nDefinition von Sichten als virtuelle Tabellen,\nManipulation von Benutzerdaten,\nberprfung von Integrittsregeln,\nVergabe von Benutzerrechten und Autorisierung,\nTransaktionskontrolle und Transaktionshandling \n\nRegel 6: Benutzersichten und Datennderungen\nFr unterschiedliche Benutzergruppen und Anwendungen sind unterschiedliche\nSichten (Views) auf die Datenbank notwendig. In einfachen Views, z.B. Teilansichten\neiner Tabelle, sollen auch Datennderungen mglich sein.\n\nRegel 7: HIGH-LEVEL INSERT, UPDATE und DELETE\nIn der Datenbank muss das Einfgen, ndern und Lschen von Daten mglich sein.\nDabei soll das System sich den optimalen Zugriffspfad zur schnellen Durchfhrung\nder Transaktion selbst suchen.\n\nRegel 8: Physische Datenunabhngigkeit\nAnwendungsprogramme und Anwenderoberflchen bleiben unverndert, wenn Vernderungen\nan der Speicherstruktur oder der Zugriffsmethode in der Datenbank vorgenommen werden.\n\nRegel 9: Logische Datenunabhngigkeit\nAnwendungsprogramme und Anwenderoberflchen bleiben unverndert, wenn sich\nBasisrelationen verndern, die nicht direkt die Anwendungsprogramme betreffen. \n\nRegel 10: Integrittsunabhngigkeit\nIntegrittsbedingungen, die von der Datenbank erfllt werden mssen, werden mithilfe\nder relationalen Datenbanksprache definiert, im Data Dictionary abgelegt und\nvom DBMS ausgefhrt. Diese Integrittsbedingungen gehren nicht ins Anwenderprogramm.\n\nRegel 11: Verteilungsunabhngigkeit\nEine relationale Datenbank besitzt die Verteilungsunabhngigkeit. Das heit, die Anwendungsprogramme\nbleiben unverndert, wenn die verteilte Datenhaltung auf mehreren\nRechnern eingefhrt oder wieder zurckgenommen wird und mehrere Datenbanken zu\neiner Datenbank zusammengelegt werden.\n\nRegel 12: Unterwanderungsverbot\nFalls das DBMS eine andere 3GL-Sprache wie C oder Java zulsst, darf diese Sprache\nnicht die aufgestellten Regeln 1 bis 11 ver',
