      '\n    Das "Normalverhalten" bei einem Integrittsfehler in SQL ist, dass dieser Fehler angezeigt wird und bei einer IMMEDIATE-Prfung die Datenmanipulation abgebrochen wird bzw. bei einer DEFERRED-Prfung die ganze Transaktion zurckgerollt wird. Dieses Verhalten wird fr Integrittsfehler ausschlielich bei NOT NULL, PRIMARY KEY, UNIQUE ausgefhrt.\n\n    \n\n    Lediglich die Fremdschlssel-Constraints bieten neben diesem "Normalverhalten" eine Option zur Fehlerkorrektur und zwar unabhngig davon, ob als Spalten- oder Tabellen-Constraint definiert. Die Fehlerkorrekturen betrifft die sog. "Dangling Tuples"-Problematik,wenn Masterdatenstze gelscht werden, fr die noch abhngige Detail-Datenstze bestehen. Es besteht dann die Mglichkeit, z.B. die abhngigen Detaildatenstze ebenfalls zu lschen (CASCADE) oder deren Fremdschlsselwert auf einen vorgegebenen Wert bzw. auf NULL zu setzen (DEFAULT/NULL)...\n\n    \n\n     ::=\n\n    REFERENCES Tabellenname [ ( Spaltenname [ , Spaltenname ]... ) ]\n\n    <bereinstimmungstyp>\n\n    [ <fehlerkorrektur definition=""> ]\n\n    \n\n    <fehlerkorrektur definition=""> ::=\n\n    ON UPDATE <fehlerkorrektur aktion="">\n\n    | ON DELETE <fehlerkorrektur aktion="">\n\n    \n\n    <fehlerkorrektur aktion=""> ::=\n\n    CASCADE | SET NULL | SET DEFAULT | RESTRICT | NO ACTION\n\n    \n\n    Wenn man bedenkt, fr welche Problemstellung die Fehlerkorrektur gedacht ist, dann kommt man schnell darauf, das es nur die beiden Klauseln "ON UPDATE" und "ON DELETE" geben kann.\n\n    Werden Fremdschlssel verwendet, so kann es zur sog. "Dangling Tuples"-Problematik kommen, bei der es darum geht, dass Master-Datenstze, die noch von Detail-Datenstze referenziert werden, gelscht oder deren Schlsselwert gendert wird und das anschlieend Detail-Datenstze existieren, fr deren Fremdschlsselwerte es dann keinen passenden Datensatz in der Master-Tabelle mehr gibt.\n\n    Das Einfgen von Master-Datenstzen ist immer unproblematisch, da eine Fremdschlsselbedingung es zulsst, dass es Master-Datenstze gibt, fr die es keine Detail-Datenstze gibt. \n',
