    text: 'Welche SQL-Befehle lsen eine IMMEDIATE-Integrittsprfung aus? ',
    explanation:
      'Ziel der Integrittsprfung sind ja semantisch korrekte Daten, d.h. Daten, die allen definierten Constraints gengen. Sind Constraints definiert und aktiviert, dann sind in der Datenbasis nur solche Daten persistent gespeichert, die die Constraints erfllen, so dass man mit 100%-iger Sicherheit sagen kann, alle Daten sind korrekt. Problematische Aktionen in diesem Zusammenhang sind dann alle Datenmanipulationen, also INSERT, UPDATE, DELETE. Anfragen (SELECTs) sind in diesem Zusammenhang unkritische Aktionen, die keine Prfung erfordern - es soll ja nichts gespeichert werden, nur angezeigt. \n\nDie drei DML-Befehle INSERT, UPDATE, DELETE lsen also IMMEDIATE-Integrittsprfung aus. \nCOMMIT beendet eine Transaktion und lst damit eine DEFERRED-Integrittsprfung aus. \nROLLBACK beendet zwar auch eine Transaktion, jedoch indem sie rckgngig gemacht wird. Eine Integrittsprfung ist daher unntig und wird nicht ausgefhrt. \nDDL-Anweisungen (CREATE, ALTER, DROP) beinhalten ein implizites COMMIT, was dafr sorgt, dass jede DDL-Anweisung unmittelbar in die DICTIONARY-Tabellen eingetragen wird. Dieses implizite COMMIT hat zur Folge, dass DDL-Anweisungen immer auch eine Transaktion beenden und eine DEFERRED-Integrittsprfung auslsen.  \nSAVE, CHECK sind keine SQL-Befehle. Es gibt wohl das CHECK-Constraint, aber eine Prfung wird damit nicht ausgelst. \n',
