      'Lesekonsistenz ist ein weiteres Problem, welches beim Mehrbenutzerbetrieb auftritt. Bei lnger andauernden Transaktionen stellt sich die Frage, auf welchem Datenbankzustand die Anfragen eigentlich ausgewertet werden, wenn parallel andere Benutzer die zu lesenden Daten ndern.\n\nDaraus resultieren mehrere Probleme: \n\nBei innerhalb einer Transaktion wiederholten Anfragen knnen bei der Wiederholung Datenstze angezeigt werden, die vorher nicht da waren, also "Phantome", bzw. es werden Datenstze nicht mehr angezeigt oder mit genderten Inhalt, also "nicht wiederholbares Lesen".  Grund dafr sind jeweils andere Transaktionen, die ihre nderungen persistent gespeichert haben (COMMIT).\n\nEin anderes Problem stellt sich bei langandauernden Anfragen, was passiert mit den nderungen parallel ausgefhrter Transaktionen, die zwischenzeitlich ihre nderungen persistent gespeichert haben. (COMMIT).\n\n"Dirty Read" ist ein Problem, das beiden Aufgabenstellungen, Nebenlufigkeitskontrolle wie auch Lesekonsistenz betrifft.\n\n"Lost Update" ist ein Problem der Nebenlufigkeitskontrolle weniger der Lesekonsistenz.\n',
